import * as React from "react";
import { Helmet } from "react-helmet";

import { useConfig } from "gatsby-theme-advanced";
import styled from "styled-components";

import Breadcrumb from "../../components/Breadcrumb";

import MDXTheme from "../../components/Article/MdxTheme";

import BaseinfoMdx from "./content/baseinfo.mdx";
import SkillMdx from "./content/skills.mdx";

import Layout from "../../layouts";
import { RenderBox } from "../../components/Article/Render";
import { breakpoints } from "../../theme/Constants";
import { LikeBoxWrapper } from "../../components/Article/Spacing";
import { H2, H4 } from "../../theme";
import LogoPng from "../../../images/logo.png";
import DownButton from "./DownButton";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;

  @media (max-width: ${breakpoints.sm}) {
    gap: 24px;
  }
`;

const ListHead = styled(H2)`
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
`;

const IndIcon = styled.img`
  width: 60px;
  height: auto;
  margin-right: 24px;

  @media (max-width: ${breakpoints.sm}) {
    width: 40px;
    margin-right: 8px;
  }
`;

const BoxDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const BoxHead = styled(H4)`
  margin-bottom: 16px;
`;

const AboutPage = (): JSX.Element => {
  const config = useConfig();

  return (
    <Layout showSidebar>
      <Wrapper>
        <Helmet title={`关于 | ${config.website.title}`} />

        <LikeBoxWrapper>
          <Breadcrumb breadcrumbs={[{ label: "关于" }]} />
        </LikeBoxWrapper>

        <ListHead>
          <IndIcon src={LogoPng} alt="indicator" />
          关于我
        </ListHead>

        <BoxDiv>
          <LikeBoxWrapper>
            <BoxHead>个人简介</BoxHead>
          </LikeBoxWrapper>
          <RenderBox>
            <MDXTheme slug="about">
              <BaseinfoMdx />
            </MDXTheme>
          </RenderBox>
        </BoxDiv>

        <BoxDiv>
          <LikeBoxWrapper>
            <BoxHead>技能介绍</BoxHead>
          </LikeBoxWrapper>
          <RenderBox>
            <MDXTheme slug="about">
              <SkillMdx />
            </MDXTheme>
          </RenderBox>
        </BoxDiv>

        <DownButton />
      </Wrapper>
    </Layout>
  );
};

export default AboutPage;
